<template lang="html">
  <div class="zfsm">
    <div class="zfsm-wrapper">
      <div class="zfsm-wrapper-top">
        <div class="zfsm-wrapper-top-l">
          <span>资费说明</span>
          <span>/</span>
          <span>说明说明说明说明说明说明说明说明</span>
        </div>

          <router-link to="/YFZH" tag="div" class="zfsm-wrapper-top-r">返回上一页
          <i class="el-icon-arrow-right"></i></router-link>


      </div>
      <div class="zfsm-wrapper-bottom">
        <div class="table">
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="fw" label="服务项目" width="180"></el-table-column>
            <el-table-column prop="sf" label="收费标准" width="180"></el-table-column>
            <el-table-column prop="bz" label="备注说明"></el-table-column>
          </el-table>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
       return {
         tableData: [
           {
             fw: '手机定位',
             sf: '0.2元/次',
             bz: '定位失败不扣费'
          },
          {
            fw: '手机短信',
            sf: '0.1元/条',
            bz: '发送失败不扣费'
         },
         {
           fw: '身份认证',
           sf: '5元/次',
           bz: '操作失败不扣费'
         },
         {
           fw: '货运保险',
           sf: '费率千分之一',
           bz: '平安运输损失险-普通货物-综合消费标准'
        }
       ]
       }
     }
}
</script>

<style lang="less" scoped>
.zfsm{
  background-color: #FAFBFF;
  border-top:1px solid #e6e9f2;
  .zfsm-wrapper{
    width: 1540px;
    margin: 0 auto;
    .zfsm-wrapper-top{
      width: 1540px;
      display: flex;
      justify-content: space-between;
      height: 90px;
      line-height: 90px;
      .zfsm-wrapper-top-l{
        span:nth-of-type(1){
          font-size: 20px;
          color: #3257a7;
          font-weight: 700;
        }
        span:nth-of-type(2){
          margin: 0 5px;
        }
        span{
          font-size: 16px;
          color: #9ba5be;
        }
      }
      .zfsm-wrapper-top-r{
        font-size: 12px;
        color: #a5a4a9;
      }
    }
    .zfsm-wrapper-bottom{
      background-color:#fff;
      border:1px solid #e6e9f2;
      .table{
        width: 1476px;
        margin: 30px auto;
        margin-bottom: 500px;
      }
    }
  }
  .el-table th>.cell{
    color: #828eb6;
  }
}
</style>



